.SubscribeCom h2 {
  font-family: Montserrat;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: -0.02em;
  text-align: center;
  max-width: 430px;
  letter-spacing: 1px;
  margin: 10px 0px;
}

@media (max-width: 500px) {
  .SubscribeCom {
    padding: 0px 10px;
    margin: 70px 0px !important;
  }
  .SubscribeCom_input {
    min-width: 100% !important;
  }
  .SubscribeCom h2 {
    font-size: 33px;
    width: 100% !important;
  }
}
